import { validWinLogin } from '@/api/'
export default {
  data () {
    return {
    }
  },
  methods: {
    login () {
      validWinLogin({id: this.$route.query.id}).then((res) => {
        this.$router.replace({path: '/MyBars'})
      }).catch((error) => {
        alert(error)
      })
    }
  }
}



